import React, {Component} from "react";

export const MobileNumberSection = (props) => {
    return (<div>
        {props.numberLabel}&nbsp;
        <span className="u-font-bold">{getFormattedNumber(props.product.phoneNumber)}

            {(props.editable === true) &&
            <a href="#" onClick={props.handleShowModalVoip} className="u-spacing-left">
                {props.changeNumberLabel}
            </a>
            }

        </span>
    </div>)
}

export const NumberComponent = (props) => {
    let phoneNumber = (props.selectedNumberVoip != "") ?
    props.selectedNumberVoip
    :
    props.product.phoneNumber;
    phoneNumber = phoneNumber ? getFormattedNumber(phoneNumber) : null;
    return phoneNumber ? (<div>
        {props.numberLabel}&nbsp;
        {phoneNumber}

        {(props.editable === true) &&
        <a href="#" onClick={props.handleShowModalVoip}
           className="u-spacing-left">{props.changeNumberLabel}</a>
        }

    </div>) : null
};

export const ChangeComponent = (props) => {
    return (!!props.onChange && !!props.changeLabel) ?
        <div className='u-padding-top'>
            <a href="#" className="u-spacing-right-l u-text-nowrap" onClick={props.onChange}>
                {props.changeLabel}
            </a>
        </div>
        : null
};

export const transformFirstBillInfo = (price) => {
    if(price) {
        return ({
            integer: price.gross.split(',')[0],
            fraction: price.gross.split(',')[1],
            currency: price.currency
        })
    } else {
        return null;
    }
}

export const transformMonthlyPriceInfo = (prices) =>{
    var monthlyPrices = prices.map((charge) => {
        return ({
            integer: charge.gross.split(',')[0],
            fraction: charge.gross.split(',')[1],
            currency: charge.currency,
            start: charge.monthFrom,
            end: charge.monthTo
        });
    });
    return monthlyPrices;
}

export const getFormattedNumber = (number) => {
    let formattedNumber = "";
    if (number) {
        formattedNumber = number.substr(0,2) + " " + number.substr(2,3) + " " + number.substr(5,2) + " " + number.substr(7,2);
    }
    return formattedNumber;
}

export const Graphic = (props) => {
    if(props.imgUrl){
        return (
            <div>
                <img src={props.imgUrl}/>
            </div>
        )
    } else {
        return (
            <span aria-hidden="true"
                  className={"g-icon g-icon--only g-icon--"+props.icon}>
            </span>
        )
    }
}

export const GraphicCell = (props) => {
    return (
        <td className="opl-checkout__icon__cell">
            <Graphic icon={props.icon ? props.icon.toLowerCase().split('_').join("-") : ""} imgUrl={props.imgUrl}/>
        </td>)
}